import { useState } from "react";
import * as XLSX from "xlsx";

import './App.css';

function App() {
  const [data, setData] = useState([]);
  /* const [parsedDataString, setParsedDataString] = useState(""); */
  /* const [selectedRow, setSelectedRow] = useState(null); */

  const getCurrentYear = () => {
    const today = new Date();
    const currentMonth = today.getMonth() + 1; // Months are zero-based in JavaScript (January is 0)
    const currentYear = today.getFullYear();

    return currentMonth <= 3 ? currentYear - 1 : currentYear;
  }

  const getNextYear = () => {
    const today = new Date();
    const currentYear = today.getFullYear();

    return currentYear;
  }

  const getVersionType = (rowYear) => {
    const currentYear = getCurrentYear();
    return rowYear === currentYear ? "Public.Revised" : "Public.Estimate";
  }

  const processFileDuplicate = (data) => {
    const workbook = XLSX.read(data, { type: "binary" });
    const sheetName = workbook.SheetNames[0];
    const sheet = workbook.Sheets[sheetName];
    const parsedData = XLSX.utils.sheet_to_json(sheet);
    setData(parsedData);
    /* setParsedDataString(JSON.stringify(parsedData, null, 2)); */
  
    // Identify the row to extract as a JSON string
    /* const selectedRowData = parsedData.find(row => row.__EMPTY === "Ministry View"); */
  
    // Set the selected row as a JSON string
    /* if (selectedRowData) {
      setSelectedRow(JSON.stringify(selectedRowData, null, 2));
    } */
  
    // Prepare data for export
    const exportData = [
      [parsedData[1]?.__EMPTY, parsedData[1]?.__EMPTY_1, "Account", "Date", "Version", "Amount"],
      ...parsedData.slice(2).map((row, rowIndex) => [
        row.__EMPTY,
        row.__EMPTY_1,
        row.Measures,
        getCurrentYear(),
        getVersionType(getCurrentYear()),
        row["Revised CFY"]
      ])
    ];
  
    // Add the second set of data with NextYear and Estimated NFY
    const extendedExportData = [
      ...exportData,
      ...parsedData.slice(2).map((row, rowIndex) => [
        row.__EMPTY,
        row.__EMPTY_1,
        row.Measures,
        getNextYear(),
        getVersionType(getNextYear()),
        row["Estimated NFY"]
      ])
    ];
  
    const ws = XLSX.utils.aoa_to_sheet(extendedExportData);
  
    // Add custom formatting for headers
    ws["A1"].s = { font: { bold: true } };
    ws["B1"].s = { font: { bold: true } };
    ws["C1"].s = { font: { bold: true } };
    ws["D1"].s = { font: { bold: true } };
    ws["E1"].s = { font: { bold: true } };
    ws["F1"].s = { font: { bold: true } };
  
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
    XLSX.writeFile(wb, "exported_data.xlsx");
  }

  const handleFileUpload = (e) => {
    const reader = new FileReader();
    reader.readAsBinaryString(e.target.files[0]);
    reader.onload = (e) => {
      const data = e.target.result;
      processFileDuplicate(data);
    };
  }

  return (
    <div className="App">
      <input 
        type="file" 
        accept=".xlsx, .xls" 
        onChange={handleFileUpload} 
      />
    </div>
  );
}

export default App;
